import {colors} from '../utils/styles'
import {Affix, Button, Menu, Dropdown, Icon} from 'antd';
import 'antd/dist/antd.css'

const styles = {
    registrationButton: {
        borderRadius: '40px',
        marginRight: 40
    },
    menuButton: {
        borderRadius: '40px'
    },
    navigationWrapper: {
        color: colors.primary.dark,
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        height: 80,
        width: '100%',
        backgroundColor: 'white',
        top: 0,
        boxShadow: '0px 3px 4px -2px rgba(117,117,117,1)',
        zIndex: 100
    },
    navLogo: {
        height: 30,
        marginLeft: 40
    }
}

const menu = (
    <Menu>
        <Menu.Item>
            <a rel='noopener noreferrer'>1st menu item</a>
        </Menu.Item>
        <Menu.Item>
            <a rel="noopener noreferrer">2nd menu item</a>
        </Menu.Item>
        <Menu.Item>
            <a rel="noopener noreferrer">3rd menu item</a>
        </Menu.Item>
    </Menu>
);

export default() => {
    return (
        <Affix offsetTop={0}>
            <div style={styles.navigationWrapper}>
                <img
                    src='https://www.russiancenterny.org/wp-content/uploads/2018/09/yfny-logo-sign.svg'
                    alt="YFNY Logo"
                    style={styles.navLogo}/>
                <Dropdown overlay={menu}>
                    <Button style={styles.menuButton}>
                        Главное меню
                        <Icon type="down"/>
                    </Button>
                </Dropdown>
                <Button type='primary' style={styles.registrationButton}>Регистрация</Button>
            </div>
        </Affix>
    )
}



